    width: 100%;
    color: #FFFFFF;
    background: #FFF;
    -webkit-box-shadow: 0px 5px 17px -5px rgba(128, 128, 128, 1);
    -moz-box-shadow: 0px 5px 17px -5px rgba(128, 128, 128, 1);
    box-shadow: 0px 5px 17px -5px rgba(128, 128, 128, 1);
}

.tendercart-type__status-public {
    display: inline-block;
    text-decoration: none;
    background-color: #e7ffd2;
    color: #60ad1e;
    border-radius: .3rem;
    margin-top: 0;
    padding: .4rem 1.5rem;
    min-width: 10rem;
    text-align: center;
    box-sizing: border-box;
    font-size: .9rem;
    font-weight: 500;
    margin-right: 1rem;
}

input[type="checkbox"]:required:invalid+label {
    color: #ff0000;
}

input[type="checkbox"]:required:valid+label {
    color: #254906;
}

input[type="text"]:required:invalid+label {
    color: #ff0000;
}

input[type="text"]:required:valid+label {
    color: #254906;
}

.tendercart-type__status-public.canceled {
    background-color: #ff0000;
    color: #FFF;
}

.tendercart-type__status-public.goodend {
    background-color: #254906;
    color: #FFF;
}

.tendercart-type__status-public.pending,
.tendercart-type__status-public.waiting_for_capture {
    background-color: #cccccc;
    color: #000;
}

.orderid__item-settings {
    position: relative;
    display: inline-block;
    margin-left: 1rem;
    font-weight: 800;
    font-size: 1rem;
    line-height: 3;
    color: #001f31;
}

.reference._public {
    color: #656d72;
}

.reference {
    position: relative;
    padding-left: 0rem;
    font-size: 0.8rem;
    font-weight: 800;
    color: rgba(0, 31, 49, 0.3);
    text-transform: uppercase;
}

.b-tender__block--title {
    position: relative;
    margin-top: 1.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.b-tender__title {
    width: 38.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1.25;
    color: #001f31;
    display: block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.b-tender__title._wide {
    width: 55rem;
}

.b-tender__title>a,
.b-tender__title>span {
    color: #001f31;
    margin-right: .5rem;
    display: inline;
    border-bottom: 1px solid rgba(0, 31, 49, .2);
}

.b-tender__info-item-text {
    font-size: 1.6rem;
    line-height: 1.56;
    color: #001f31;
}

.b-tender__block--title .b-tender__info-item-text {
    font-size: 1.8rem;
}

.rub:before {
    content: "₽";
}

.b-tender__block--title .b-tender__info-item-text .rub {
    font-weight: 400;
}

.b-tender__info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.b-tender__info-item {
