.order__header__settings__mainblock {
    position: relative;
}

.order__header__settings {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
}

.order__header__openSettings {
    position: relative;
    width: 100%;
    max-width: 366px;
    margin: 0 auto;
    background: #FFF;
    border-radius: 8px 8px 0px 0px;
    -moz-border-radius: 8px 8px 0px 0px;
    -webkit-border-radius: 8px 8px 0px 0px;
    border: 0px solid #000000;
    cursor: pointer;
    color: #f15a24;
    text-transform: uppercase;
    text-align: center;
    padding: 7px 15px;
}

.order__header__openSettings .btn {
    border-radius: 0px 8px 0px 8px;
    -moz-border-radius: 0px 8px 0px 8px;
    -webkit-border-radius: 0px 8px 0px 8px;
