.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    background-color: #FFF;
    overflow-x: hidden;
    padding-top: 48px;
    transition: 0.5s;
    -webkit-box-shadow: 6px 0px 7px -6px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 6px 0px 7px -6px rgba(0, 0, 0, 0.75);
    box-shadow: 6px 0px 7px -6px rgba(0, 0, 0, 0.75);
}

.sidenav a {
    padding: 12px 8px 12px 32px;
    text-decoration: none;
    font-size: 14px;
    color: #424141;
    text-transform: uppercase;
    display: block;
    transition: 0.3s;
}

.sidenav a:hover {
    color: #FFF;
    background-color: #fbb040;
}

.sidenav a.active,
.sidenav a.active:hover {
    color: #FFF;
    background-color: #2bb673;
}

.sidenav .closebtn {
    position: absolute;
    background-color: #f1f1f1;
    top: 0;
    left: 0;
    font-size: 36px;
    line-height: 36px;
    color: #424141;
    width: 100%;
    padding: 5px 15px 7px 15px;
    margin: 0;
    text-align: right;
}

