/* ENTER PAGE */

.wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 100%;
    padding: 20px;
}

#formContent {
    -webkit-border-radius: 10px 10px 10px 10px;
    border-radius: 10px 10px 10px 10px;
    background: #f2f2f2;
    padding: 30px;
    width: 90%;
    max-width: 450px;
    position: relative;
    padding: 0px;
    -webkit-box-shadow: 0 30px 60px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 30px 60px 0 rgba(0, 0, 0, 0.3);
    text-align: center;
}

#formContent.lg {
    max-width: 1000px;
}

.form-control:focus {
    border-color: #5a9530 !important;
    box-shadow: 0 0 5px #5a9530 !important;
}

#formFooter {
    background-color: #f6f6f6;
    border-top: 1px solid #dce8f1;
    padding: 25px;
    text-align: center;
    -webkit-border-radius: 0 0 10px 10px;
    border-radius: 0 0 10px 10px;
}

#formFooter a {
    color: #5a9530;
}

.step-number__count {
    font-size: 20px;
    color: #ffffff;
    width: 33px;
    height: 33px;
    -webkit-border-radius: 50%;
    -webkit-background-clip: padding-box;
    -moz-border-radius: 50%;
    -moz-background-clip: padding;
    border-radius: 50%;
    background-clip: padding-box;
    background: #518e27;
    display: inline-block;
    vertical-align: middle;
    margin-top: -6px;
}

.step-number__title {
    font-size: 15px;
    font-weight: 700;
    text-transform: uppercase;
    margin: 0 0 0 10px;
    color: #3d3d3d;
}

label.min-line-height {
    line-height: 16px;
}

/* ANIMATIONS */

/* Simple CSS3 Fade-in Animation */

.underlineHover:after,
.underlineHover.red:after {
    display: block;
    left: 0;
    bottom: -10px;
    width: 0;
    height: 2px;
    background-color: #5a9530;
    content: "";
    transition: width 0.2s;
}

#formFooter a.uHred {
    color: #ea492c;
}

.underlineHover.uHred:after {
    background-color: #ea492c;
}

.underlineHover:hover {
    color: #0d0d0d;
}

.underlineHover:hover:after {
    width: 100%;
}

/*PAGINATIONS*/
.page-item.active .page-link {
    background-color: #e7ffd2;
    border-color: #e7ffd2;
    color: #5a9530;
}

.page-link {
    color: #5a9530;
}

.page-link:hover {
